body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.link-in-body {
  color: navy;
}
section {
    min-height: 30vh;
}

.App {
    background-color: lightsteelblue;
}

.nav-text {
    font-weight: bold;
}

a {
    color: black;
}

a:hover, a:focus {
    opacity: .5;
    -webkit-transition: opacity .15s ease-in;
    transition: opacity .15s ease-in;
}

footer {
    margin-top: 30px;
    word-wrap: break-word;
}

#contacts-title {
    background-color: whitesmoke;
    margin: 0;
    text-align: center;
}

#bootstrap-title{
    background-color: lightskyblue;
    margin: 0;
    text-align: center;
}

#mern-title {
    background-color: black;
    margin: 0;
    text-align: center;
    color: white;
}

.icon {
    max-width: 50px;
}

ul.contact-icon {
    padding: 0;
}
li.contact-icon {
    display: inline;
    margin: 2px;
}

#tachyons-image {
    max-height: 138 px;
    max-width: 250 px;
}
#portrait {
    max-width: 300px;
    max-height: 300px;
    padding: 30px;
}

#quote {
    display: flex;
    align-items: center;
    justify-content: center;
}
.slideshow-container {
    max-width: 60%;
    max-height: 60%;
    text-align: "center";
}


h1 {
    text-align: center;
}

.musical {
    text-align: center;
    display: flex;
    justify-content: center;
}

audio::-internal-media-controls-download-button {
    display: none;
}
.project-picture {
    max-width: 80%;
    padding: 20px;
    display: flex;
    align-items: center; 
    flex-wrap: wrap;
}


.project_description {
    align-items: center;
}

.portfolio-row {
    min-height: 300px;
}
#response-box {
    background-color: rgb(250, 250, 94);
    min-height: 200px;
    padding: 25px;
    border: 1px solid black;
    border-radius: 20px;
}

textarea {
    border-radius: 20px;
    padding: 25px;
    min-height: 100px;
}

#luis-submit-button {
    
}
#main-headline {
    background-color: navy;
    padding: 20px;
    color: white;
    text-align: center;
}

